.page {
  max-width: 880px;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  background-color: #000;
  font-family: "Inter", Arial, sans-serif;
  font-weight: 400;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}
.main {
  flex: 1 1;
}

@media screen and (max-width: 979px) {
  .main {
    padding: 0 19px;
  }
}
.header {
  min-height: 74px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  margin: 0;
  padding: 20px 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  transition: 0.5s;
}

@media screen and (max-width: 979px) {
  .header {
    min-height: 56px;
    padding: 14px 0 14px 27px;
  }
}
.header__link {
  transition: 0.5s;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  color: #fff;
  line-height: 22px;
  margin: 0;
  padding: 0;
}

.header__link:hover {
  opacity: 0.6;
}

@media screen and (max-width: 979px) {
  .header__link {
    font-size: 13px;
  }
}
.header__logo {
  max-width: 142px;
  height: 33px;
  transition: 0.5s;
}

@media screen and (max-width: 979px) {
  .header__logo {
    width: 103.74px;
    height: 24.4px;
  }
}
.header__auth {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 979px) {
  .header__auth {
    margin-right: 15px;
    flex-wrap: wrap;
    justify-content: right;
  }
}
.profile {
  display: flex;
  padding: 40px 0 50px;
  align-items: center;
}

@media screen and (max-width: 979px) {
  .profile {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 42px 0 36px;
  }
}
.profile__add-btn {
  width: 150px;
  min-height: 50px;
  background: url(../../static/media/plus.bdfb8ebb.svg) no-repeat center;
  border: 2px solid #fff;
  padding: 0;
  margin: 0 0 0 auto;
  transition: 0.5s;
}

.profile__add-btn:hover {
  cursor: pointer;
  opacity: 0.6;
}

@media screen and (max-width: 979px) {
  .profile__add-btn {
    margin: 36px auto 0;
    width: 282px;
    background-size: 16px 16px;
  }
}
.profile__avatar-zone {
  display: flex;
  position: relative;
  margin: 0;
  padding: 0;
}
.profile__avatar-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
.profile__avatar-edit-btn {
  position: absolute;
  width: 120px;
  height: 120px;
  border: none;
  border-radius: 50%;
  background: none;
  margin: 0;
  padding: 0;
  /*transition: 0.5s;*/
}

.profile__avatar-edit-btn:hover {
  background: rgba(0, 0, 0, 0.9) url(../../static/media/avatar-edit-icon.34c46850.svg) no-repeat center center;
  cursor: pointer;
  opacity: 0.8;
}
.profile__description {
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  grid-area: description;
}

@media screen and (max-width: 979px) {
  .profile__description {
    font-size: 14px;
    line-height: 17px;
    margin-bottom: -5px;
  }
}
.profile__edit-btn {
  width: 24px;
  height: 24px;
  border: none;
  margin-bottom: 2px;
  background: url(../../static/media/edit-icon.c8252db2.svg) no-repeat center center/cover;
  grid-area: button;
  transition: 0.5s;
}

.profile__edit-btn:hover {
  cursor: pointer;
  opacity: 0.6;
}

@media screen and (max-width: 979px) {
  .profile__edit-btn {
    width: 18px;
    height: 18px;
  }
}
.profile__info {
  max-width: 450px;
  display: grid;
  grid-template-columns: auto 24px;
  grid-gap: 16px 18px;
  gap: 16px 18px;
  grid-template-areas: "name button" "description .";
  align-items: end;
  margin: 0 0 0 30px;
}

@media screen and (max-width: 979px) {
  .profile__info {
    margin: 26px 19px 0;
    grid-gap: 14px 10px;
    gap: 14px 10px;
    grid-template-areas: "name button" "description description";
  }
}
.profile__name {
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  grid-area: name;
}

@media screen and (max-width: 979px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    margin-bottom: -5px;
  }
}
.elements-grid {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  grid-gap: 20px 17px;
  gap: 20px 17px;
  justify-content: center;
  justify-items: stretch;
  margin: 0 auto;
}
.element {
  width: 100%;
  border-radius: 10px;
  background-color: #fff;
  line-height: 0;
  position: relative;
}
.element__description {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 11px 15px 10px 21px;
  /*margin: 25px 15px 31px 21px;*/
}
.element__image {
  width: 100%;
  height: 282px;
  min-height: 282px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}
.element__likes {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 10px 0 10px 0;
}
.element__like {
  min-width: 22px;
  height: 19px;
  border: none;
  background: url(../../static/media/like-icon.cb5f1643.svg) no-repeat center;
  padding: 0;
  margin: 0;
  transition: 0.5s;
}

.element__like:hover {
  cursor: pointer;
  opacity: 0.5;
}

.element__like_active {
  background: url(../../static/media/like-icon-active.933106ea.svg) no-repeat center;
  opacity: 1;
}
.element__likes-counter {
  color: #000;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  margin: 0;
  padding: 3px 0 0 0;
  text-align: center;
}
.element__title {
  font-size: 24px;
  line-height: 29px;
  font-weight: 900;
  margin: 0 0 -5px 0;
  color: #000;
  max-width: 85%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.element__trash {
    min-width: 18px;
    height: 19px;
    border: none;
    background: url(../../static/media/delete-icon.9c3b03ae.svg) no-repeat center;
    padding: 0;
    margin: 0;
    transition: 0.5s;
    position: absolute;
    top: 20px;
    right: 20px;
}

.element__trash:hover {
    cursor: pointer;
    opacity: 0.5;
}
.footer {
  margin: 0;
  padding: 66px 0 60px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 979px) {
  .footer {
    padding: 48px 19px 36px;
  }
}
.footer__copyright {
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 979px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility .5s, opacity .5s ease;
}

.popup__close-btn {
  position: absolute;
  top: -37px;
  left: 102%;
  width: 32px;
  height: 32px;
  background: url(../../static/media/close-icon.45a5fb8f.svg) no-repeat center/cover;
  border: none;
  margin: 0;
  padding: 0;
  transition: opacity 0.5s;
}

.popup__close-btn:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 979px) {
  .popup__close-btn {
    width: 20px;
    height: 20px;
    top: -35px;
    left: 93%;
  }
}
.popup__container {
  background-color: #fff;
  position: relative;
  max-width: 430px;
  width: 100%;
  padding: 34px 36px 37px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  transition: .5s;
}

@media screen and (max-width: 979px) {
  .popup__container {
    max-width: 282px;
    padding: 25px 22px 25px;
  }
}
.popup__fieldset {
  border: none;
  margin: 20px 0 15px;
  padding: 0;
  width: 100%;
}

@media screen and (max-width: 979px) {
  .popup__fieldset {
    margin: 75px 0 15px;
  }
}
.popup__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.popup__heading {
  color: #000;
  font-size: 24px;
  line-height: 29px;
  font-weight: 900;
  margin: 0;
  align-self: flex-start;
  padding-bottom: 31px;
}

@media screen and (max-width: 979px) {
  .popup__heading {
    font-size: 18px;
    line-height: 22px;
  }
}
.popup__input {
  width: 100%;
  font-size: 14px;
  line-height: 17px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 0 9px;
  margin-top: 0;
  box-sizing: border-box;
}
.popup__input_type_error {
    border-bottom: 1px solid red;
}
.popup__save-btn {
  width: 100%;
  max-height: 50px;
  min-height: 50px;
  color: #fff;
  padding: 0;
  background-color: #000;
  border: none;
  border-radius: 2px;
  font-size: 18px;
  line-height: 22px;
  transition: opacity 0.5s;
}

.popup__save-btn:hover {
  opacity: 0.8;
  cursor: pointer;
}

@media screen and (max-width: 979px) {
  .popup__save-btn {
    max-height: 46px;
    min-height: 46px;
    font-size: 14px;
    line-height: 17px;
  }
}
.popup__save-btn_disabled {
    color: #000;
    border: 1px solid #000;
    border-radius: 2px;
    background-color: transparent;
    opacity: .3;
    pointer-events: none;
    -webkit-user-select: none;
            user-select: none;
}
.popup_opened {
  display: flex;
  opacity: 1;
  visibility: visible;
}
.popup_dark-overlay {
    background-color: rgba(0, 0, 0, 0.9);
}
.popup__container-image {
    position: relative;
}
.popup__image {
    max-width: 75vw;
    max-height: 75vh;
}
.popup__image-caption {
    font-size: 12px;
    line-height: 15px;
    margin-top: 10px;
    padding: 0;
}
.popup__error {
    font-size: 12px;
    line-height: 15px;
    color: #FF0000;
    display: flex;
    padding-top: 5px;
    min-height: 28px;
    visibility: hidden;
}



.popup__error_visible {
    visibility: visible;
}
.auth {
  max-width: 358px;
  margin: 60px auto 0;
  padding: 0;
  min-height: 590px;
}
.auth__title {
  font-size: 24px;
  font-weight: 900;
  line-height: 29px;
  text-align: center;
  margin: 0 0 50px 0;
  padding: 0;
}
.auth__label {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}
.auth__input {
  font-size: 14px;
  font-weight: 400;
  line-height: 15px;
  color: #ccc;
  margin: 0;
  padding: 0 0 10px;
  border: none;
  border-bottom: 2px solid #ccc;
  outline: none;
  background-color: black;
}
.auth__save-btn {
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  border: none;
  border-radius: 2px;
  margin: 180px 0 0;
  padding: 0;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.auth__save-btn:hover {
  opacity: 0.8;
}
.auth__link {
  color: #fff;
  text-decoration: none;
  margin: 0;
  padding: 0;
}
.auth__go-to-login {
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 8px;
  gap: 8px;
  margin: 0;
  padding: 0;
}
.auth__tooltip {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
}
.auth__tooltip-icon {
  max-width: 120px;
  max-height: 120px;
  margin: 26px 0 32px;
}
.auth__tooltip-text {
  color: #000;
  font-size: 24px;
  font-weight: 900;
  line-height: 29px;
  text-align: center;
  margin: 0;
  padding: 0;
}

.auth__tooltip-text:last-child {
  margin-bottom: 23px;
}
